const watchNow = (() => {
  let key = -1;

  return (isStart: boolean, callback: (x: string) => void) => {
    clearInterval(key);

    if (isStart) {
      key = window.setInterval(() => {
        const date = new Date();

        callback(`${String(date.getHours()).padStart(2, '0')}:${String(date.getMinutes()).padStart(2, '0')}`);
      }, 1000);
    }
  };
})();

